    width: 100%;
} */

body>#root>.app>.app-home>.img-pview-area{
    position: relative;
    margin-top: var(--mb-0-25);
    max-width: 100%;
    max-height: calc(var(--vh, 1vh) * 75);
    height: calc(var(--vh, 1vh) * 75);
    box-sizing: border-box;
    background-color: rgb(235, 233, 229);
    border-radius: var(--mb-0-5);
}


body>#root>.app>.app-home>.action-btn-grp{
    margin-top: var(--mb-0-5);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: var(--mb-0-5);
}
