body>#root>.app>.app-home>.img-pview-area>.image-view-area>.view-item{
    display: flex;
    background-color: rgba(223, 220, 215, 0.979);
    border-radius: var(--mb-0-5);
    justify-content: center;
}

body>#root>.app>.app-home>.img-pview-area>.image-view-area>.view-item>.msg-sec{    
    color: var(--text-color-light);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

body>#root>.app>.app-home>.img-pview-area>.image-view-area>.view-item>.msg-sec>input[type=file]{
    visibility: hidden;
    position: absolute;
}

body>#root>.app>.app-home>.img-pview-area>.image-view-area>.view-item>.img-area{
    position: relative;
    display: grid;
    place-items: center;
}

body>#root>.app>.app-home>.img-pview-area>.image-view-area>.view-item>.img-area>.preview-img{
    max-height: 100%; 
    max-width: 100%;
    display: block;
    /* object-fit: contain; */
}

body>#root>.app>.app-home>.img-pview-area>.image-view-area>.view-item>.img-area>.utility{
    position: absolute;
    bottom:  0%;
    left: 0%; 
    height: 10%; 
    width: 100%;
    padding: 0 var(--mb-0-5);
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    background-color: rgba(54, 54, 53, 0.5);
    color: var(--text-color-light);
}



@media screen and (max-width: 767px) {
   
    .size-control-1{
        height: 75%;
        max-height: 85%;
        margin: var(--mb-0-5);
    }
    
    .size-control-2{
        height: 45%;
        max-height: 45%;
        padding: var(--mb-0-5);
    }
    
    .size-control-3{
        height: 30%;
        max-height: 30%;
        padding: var(--mb-0-5);
        font-size: var(--small-font-size);
        
    }
}

@media screen and (min-width: 768px) {
    body>#root>.app>.app-home>.img-pview-area>.image-view-area>.view-item{
        
        min-height: 50%;   
        max-height: 95%;   
    }
    .size-control-1{
        width: 75%;
        max-width: 85%;
        margin: var(--mb-0-5);
    }
    
    .size-control-2{
        width: 45%;
        max-width: 45%;
        padding: var(--mb-0-5);
    }
    
    .size-control-3{
        width: 30%;
        max-width: 30%;
        padding: var(--mb-0-5);
        font-size: var(--small-font-size);
    }    
}