body>#root>.app>.app-home>.img-pview-area>.image-view-area{
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}


@media screen and (max-width: 767px) {
    body>#root>.app>.app-home>.img-pview-area>.image-view-area{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
}

@media screen and (min-width: 768px) {
    body>#root>.app>.app-home>.img-pview-area>.image-view-area{
        
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
    }
}