body>#root>.app>header{
    position: relative;
    top: 0;
    left:0;
    transition: top 0.3s;
    width: 100%;
    z-index: var(--z-fixed);
    background-color: var(--body-color);
    box-shadow: 0 -1px 4px hsl(var(--hue-color),69%,61%);
    color: var(--first-color);
}

body>#root>.app>header.app-online{
    background-color: transparent
}

body>#root>.app>header.app-offline{
    background-color: orange;
}

body>#root>.app>header>nav{
    height: var(--header-height);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

body>#root>.app>header>nav .nav__link{
    font-size: var(--small-font-size);
    color: var(--text-color);
    font-weight: var(--font-medium);
    transition: color 300ms ease-out;
}

body>#root>.app>header>nav .nav__link:focus,
body>#root>.app>header>nav .nav__link:hover{
    color: var(--first-color-alt);
}

body>#root>.app>header>nav>.nav__brands{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
body>#root>.app>header>nav>.nav__brands>span{
    display: inline-block;
    margin-left: var(--mb-0-75);
}

body>#root>.app>header>nav>.nav__brands>.brand-icon{
    font-size: var(--h3-font-size);
}

body>#root>.app>header>nav>.nav__brands>.brand-logo{
    height: 2rem;
}


@media screen and (max-width: 767px) {
    body>#root>.app>header>nav>.nav__menu{
        position: absolute;
        top: var(--header-height);
        right: 0;
        width: 75%;
        background-color: var(--body-color);
        box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
        border-radius: 0 0 0 0.5rem  ;
        display: flex;
        flex-direction: column; 
        transition: 300ms ease-in-out;
    }
    body>#root>.app>header>nav>.nav__menu>.nav__item{
        line-height: 1rem;
        padding-bottom: 1rem;
        padding-top: 1rem;
    }
    body>#root>.app>header>nav>.nav__menu>.nav__item:last-child{
        padding-bottom: 1rem;
    }
    body>#root>.app>header>nav>.nav__menu>.nav__item>.nav__link{
        font-size: var(--normal-font-size);
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        color: var(--first-color);
        padding-left: var(--mb-1-5);
    }
    body>#root>.app>header>nav> .nav__btns{
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: var(--first-color);
        display: block;
        transition: ease-in-out 0.3s;
    } 
    body>#root>.app>header>nav> .hide-menu{
        display: none !important;
    } 
}

@media screen and (min-width: 768px) {
    body>#root>.app>header>nav>.nav__btns{
        display: none;
    }
    body>#root>.app>header>nav>.nav__menu{
        display: flex;
        align-items: center;
        flex-direction: row;
        background-color: transparent;
    }
    body>#root>.app>header>nav>.nav__menu>.nav__item{
        padding-left: 3rem;
        
    }
    body>#root>.app>header>nav>.nav__menu>.nav__item>.nav__link{
        position: relative;
        display: block;
        overflow: hidden;
        color: var(--first-color);
    }
    body>#root>.app>header>nav>.nav__menu>.nav__item>.nav__link.active::after{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0.1em;
        background-color: var(--first-color);  
        opacity: 1;
        transform: translate3d(0, 0, 0);
        transition: opacity 300ms, transform 300ms ease-out;
    }

    body>#root>.app>header>nav>.nav__menu>.nav__item>.nav__link::after{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0.1em;
        background-color: var(--first-color);  
        opacity: 0;
        transform: translate3d(-100%, 0, 0);
        transition: opacity 300ms, transform 300ms ease-out;
    } 
    body>#root>.app>header>nav>.nav__menu>.nav__item>.nav__link:hover::after,
    body>#root>.app>header>nav>.nav__menu>.nav__item>.nav__link:focus::after {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
    body>#root>.app>header>nav> .hide-menu{
        display: flex;
    }
}