body>#root>.app>.app-home .msg-area{
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: var(--text-color-light);
    
}

body>#root>.app>.app-home .msg-area>.msg-item>input[type=file]{
    visibility: hidden;
    position: absolute;
}

body>#root>.app>.app-home .msg-area>.msg-item>.vido-inactive
{
    visibility: hidden;
    display: none;
}


body>#root>.app>.app-home .msg-area>.msg-item>.video-show
{
    visibility: visible;
}


@media screen and (max-width: 767px) {
    body>#root>.app>.app-home .msg-area{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    body>#root>.app>.app-home .msg-area>.msg-item{
        padding: var(--mb-1-5);
        text-align: center;
    }
    
    body>#root>.app>.app-home .msg-area>.msg-item:nth-child(1){
        border-bottom: 2px solid var(--text-color-light);   
    }
}

@media screen and (min-width: 768px) {
    body>#root>.app>.app-home .msg-area{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
    body>#root>.app>.app-home .msg-area>.msg-item{
        padding: var(--mb-1-5);
        text-align: center;
        width: 45%;
    }
    body>#root>.app>.app-home .msg-area>.msg-item:nth-child(1){
        /* border-left: 2px solid var(--text-color-light); */
        border-right: 2px solid var(--text-color-light);
    }
}