.prog-area {
  height: 2px;
  width: 100%;
  user-select: none;
  overflow: hidden;
  background-color: transparent;
  position: relative;
}

.prog-area.active {
  background-color: orange !important;
}

.prog-area.active .prog-bar {
  height: 2px !important;
  background-color: var(--first-color);
  width: 0%;
  position: absolute;
  left: 0;
  animation-name: progress;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes progress {
  from {
    width: 2%;
    left: 0;
  }
  25% {
    width: 8%;
    left: 0;
  }
  50% {
    width: 16%;
    left: 0%;
  }
  75% {
    width: 32%;
    left: 0%;
  }
  90% {
    width: 50%;
    left: 20%;
  }
  to {
    width: 1%;
    left: 100%;
  }
}
