body>#root>.app>.app-history>.history-row{
    width: 100%;
    margin: var(--mb-1) 0;
}

body>#root>.app>.app-history>.history-row>.history-header{
    width: 100%;
    background-color: var(--first-color-lighter);
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: var(--mb-2-5);
    border-radius: var(--mb-0-5) 0 0 0;
}

body>#root>.app>.app-history>.history-row>.history-header>div{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

body>#root>.app>.app-history>.history-row>.history-header>div>.date,
body>#root>.app>.app-history>.history-row>.history-header>div>.taskid{
    padding: 0 var(--mb-1);
    font-weight: var(--font-semi-bold);
    color: darkslateblue
}

body>#root>.app>.app-history>.history-row>.history-header>div>.taskid>span{
    color: black;
    font-weight: var(--font-semi-bold);
}

body>#root>.app>.app-history>.history-row>.history-body{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

body>#root>.app>.app-history>.history-row:nth-child(odd)>.history-body{
    background-color: lightgray;
}

body>#root>.app>.app-history>.history-row:nth-child(even)>.history-body{
    background-color: gray;
}

body>#root>.app>.app-history>.history-row>.history-body>img{
    width: 18%;
    object-fit: contain;
}